:root {
    --spinnerColor: red;
    --bodyBgColor: #80808024;
    --bodyTxtColor: #3c4044;
    --navBgColor: #749EB2;
    --navTxtColor: #080A25;
    --navBgColor: #03bcff;
    --navTxtColor: #030A04;
    --navMenuBgColor: #36b6d7;
    --navMenuTxtColor: black;
    --btnColorDisabled: black;
    --btnBgColorDisabled: linear-gradient(to bottom, gainsboro, gainsboro);
    --btnBorderDisabled: 1px solid #00000085;
    --btnColor: #ffffff;
    --btnBgColor: linear-gradient(to bottom, #33bdef 5%, #019ad2 100%);
    --btnBorder: 1px solid #057fd0;
    --fieldSet-border: 2px solid white;
    --fieldSet-text: red;
    --selectBG_Color: white;
    --selectTxt_Color: black;
}


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    -webkit-transition: all 0.5s;
    -moz-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;
}

body {
    background-color: var(--bodyBgColor);
}

*:focus {
    outline: none;
}

.loginDiv {
    padding: calc(40vh - 110px) 8rem 1rem 12rem;
}

.App {
    padding: 0.5rem;
}

input,
select,
textarea {
    width: 100%;
    padding: 0.4rem 0.6rem;
    border-radius: 5px;
    border: thin solid grey;
    resize: none;
}

input:focus {
    box-shadow: -2px 2px var(--navMenuBgColor);
}

button {
    padding: 0.25rem 0.75rem 0.25rem 0.75rem;
    border: var(--btnBorder);
    background: var(--btnBgColor);
    color: var(--btnColor);
    border-radius: 5px;
    font-size: 1rem;
    font-weight: bold;
    margin-top: 10px;
    cursor: pointer;
    border: none;
    padding: 0.5rem 2rem;
    box-shadow: 0 4px #999;
    text-shadow: 0px -1px 0px #5b6178;
}

button:active {
    box-shadow: 0 4px #666;
    transform: translateY(4px);
    filter: brightness(0.8);
}

fieldset {
    padding: 1rem 0.5rem;
    border-radius: 10px;
    background-color: #8aadad2e;
    border: 2px solid black;
    border: var(--fieldSet-border);
    width: 100%;
    max-width: 100%;
}

fieldset legend {
    color: black;
    color: var(--fieldSet-text);
    font-weight: bold;
}






















@media only screen and (max-width: 800px) {
    .loginDiv {
        padding: calc(40vh - 110px) 1rem 1rem 4rem;
    }
}

@media only screen and (max-width: 500px) {
    .loginDiv {
        padding: calc(40vh - 110px) 1rem 1rem 1rem;
    }
}